@import './parts/reset.scss';

#camera_modal {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  min-height: 100%;
  background-color: rgba(0, 0, 0, 0.5);

  &.open {
    display: block;
  }
}

.preview {
  display: none;

  &.taken {
    display: inline;
  }
}


#camera_canvas {
  position: absolute;
  z-index: 2;
  // transform: scaleX(-1) translate(50%, 0);
  transform: translate(-50%, 0);
  top: 40px;
  left: 50%;
}

#camera_view {
  @extend #camera_canvas;

  z-index: 1;
  max-width: 100%;
}

.btn_row {
  width: 100%;
  position: absolute;
  bottom: 20px;
  text-align: center;
}
